@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2"), url("../fonts/Montserrat-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 500;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Medium.woff2") format("woff2"), url("../fonts/Montserrat-Medium.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-SemiBold.woff2") format("woff2"), url("../fonts/Montserrat-SemiBold.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Montserrat";
  font-display: swap;
  src: url("../fonts/Montserrat-Bold.woff2") format("woff2"), url("../fonts/Montserrat-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
  min-width: 320px;
  height: 100%;
  scroll-behavior: smooth;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #011c40;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  background-color: #f9fbfd;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 40px;
}
@media (max-width: 1439px) {
  .container {
    padding: 0 60px;
  }
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.product-cruise {
  position: relative;
  display: flex;
  height: 386px;
  flex-direction: column;
  justify-content: end;
  padding-top: 100%;
}
.product-cruise:hover {
  display: block;
}
.product-cruise:hover .product-cruise__box {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  z-index: 2;
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}
.product-cruise:hover .product-cruise__add-heading {
  display: none;
}
.product-cruise:hover .product-cruise__img {
  display: none;
}

.product-cruise__img {
  position: absolute;
  z-index: -1;
  top: 0;
  height: 100%;
  width: 100%;
}

.product-cruise__box {
  display: none;
  padding: 30px 25px 33px;
  height: 386px;
  box-shadow: 0 4px 35px rgba(1, 28, 64, 0.15);
  background-color: #011c40;
}
@media (max-width: 1023px) {
  .product-cruise__box {
    padding: 30px 35px 33px;
  }
}
@media (max-width: 767px) {
  .product-cruise__box {
    padding: 30px 24px 33px;
  }
}

.product-cruise__list {
  margin: 0;
  padding: 0;
  list-style: none;
  color: #f9fbfd;
}

.product-cruise__item:not(:last-child) {
  margin-bottom: 12px;
}

.product-cruise__heading {
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #f9fbfd;
  margin-bottom: 12px;
}

.product-cruise__title {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 20px;
}

.product-cruise__description {
  margin: 0;
  padding: 0;
  font-size: 14px;
  line-height: 20px;
}

.product-cruise__button {
  display: block;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  width: 100%;
  padding: 11px;
  background-color: #f9fbfd;
  align-self: flex-end;
  text-align: center;
}
@media (max-width: 767px) {
  .product-cruise__button {
    align-self: auto;
  }
}

.product-cruise__add-heading {
  margin: 0;
  padding: 0;
  font-family: "Montserrat", "Arial", sans-serif;
  font-weight: 500;
  font-size: 18px;
  line-height: 27px;
  color: #f9fbfd;
  padding: 0 0 22px 31px;
}
@media (max-width: 1023px) {
  .product-cruise__add-heading {
    padding: 0 0 27px 36px;
  }
}
@media (max-width: 767px) {
  .product-cruise__add-heading {
    padding: 0 0 17px 24px;
  }
}

@media (max-width: 767px) {
  .user-nav {
    display: none;
  }
}
@media (max-width: 767px) {
  .user-nav--menu {
    display: block;
    background-color: rgba(0, 43, 102, 0.5);
    width: calc(100% + 15px);
    left: -15px;
    align-items: flex-end;
    z-index: 20;
    position: absolute;
    top: 0;
    height: 100%;
  }
}
@media (max-width: 767px) {
  .user-nav--footer {
    position: inherit;
    background-color: transparent;
    padding: 0;
  }
}

.user-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-family: "Montserrat", "Arial", sans-serif;
  display: flex;
  gap: 33px;
  padding-top: 8px;
  position: relative;
}
@media (max-width: 1023px) {
  .user-nav__list {
    padding-top: 0;
    gap: 24px;
  }
}
@media (max-width: 767px) {
  .user-nav__list {
    display: none;
  }
}
@media (max-width: 767px) {
  .user-nav__list--menu {
    display: flex;
    margin: 0 0 0 16px;
    flex-direction: column;
    gap: 0;
    width: calc(100% - 51px);
    left: 35px;
    padding: 75px 0 10px 14px;
    height: 100%;
    background-color: #f9fbfd;
  }
}
@media (max-width: 767px) {
  .user-nav__list--footer {
    display: block;
    margin: 0;
  }
}

.user-nav__link {
  font-weight: 500;
  line-height: 20px;
  color: #f9fbfd;
  padding: 5px 0;
}
@media (max-width: 1023px) {
  .user-nav__link {
    font-size: 14px;
    line-height: 17px;
  }
}
@media (max-width: 767px) {
  .user-nav__link {
    padding: 10px;
    display: block;
    font-size: 18px;
    line-height: 22px;
    color: #011c40;
  }
}
@media (max-width: 1023px) {
  .user-nav__link--footer {
    font-size: 12px;
    line-height: 15px;
  }
}
@media (max-width: 767px) {
  .user-nav__link--footer {
    font-size: 12px;
    line-height: 15px;
    padding: 5px 0;
  }
}

@media (max-width: 767px) {
  .user-nav__item {
    margin-bottom: 8px;
  }
}

.js-menu {
  padding-left: 35px;
  word-break: break-all;
}
@media (max-width: 767px) {
  .js-menu svg {
    fill: #011c40;
  }
}

.header__box {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.header__box svg {
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .header__box svg {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .header__box svg {
    width: 42px;
    height: 22px;
  }
}

.header__button {
  display: none;
}
@media (max-width: 767px) {
  .header__button {
    position: absolute;
    right: 15px;
    display: block;
    margin-top: 5px;
    border: none;
    background-color: transparent;
    width: 24px;
    height: 14px;
    background-image: url("../img/svg/menu.svg");
  }
}
@media (max-width: 767px) {
  .header__button--close {
    width: 15px;
    height: 14px;
    background-image: url("../img/svg/close.svg");
  }
}

.about {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  column-gap: 88px;
  padding: 116px 0 145px;
}
@media (max-width: 1023px) {
  .about {
    column-gap: 21px;
    padding: 85px 0 92px;
    align-items: flex-start;
  }
}
@media (max-width: 767px) {
  .about {
    grid-template-columns: 1fr;
    padding: 50px 0 86px;
  }
}
.about::after {
  content: "";
  display: block;
  width: 560px;
  height: 301px;
  background-color: #bddefb;
  background-image: url("./../img/pic/about.webp");
  background-repeat: no-repeat;
  background-size: contain;
}
@media (min-resolution: 2dppx) {
  .about::after {
    background-image: url("./../img/pic/about@2x.webp");
  }
}
@media (max-width: 1023px) {
  .about::after {
    margin-top: 8px;
    width: 324px;
    height: 174px;
  }
}
@media (max-width: 767px) {
  .about::after {
    width: 290px;
    height: 155px;
    margin-top: 43px;
  }
}

.about__heading {
  margin: 0;
  padding: 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  margin-bottom: 25px;
  font-family: "Montserrat", "Arial", sans-serif;
}
@media (max-width: 1023px) {
  .about__heading {
    font-size: 26px;
    line-height: 36px;
    margin-bottom: 16px;
  }
}
@media (max-width: 767px) {
  .about__heading {
    font-size: 24px;
    line-height: 34px;
  }
}

.about__description {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.advantages {
  position: relative;
}
.advantages::after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
  background-color: #e3ecf6;
  background-image: url("./../img/pic/advantages-bg.webp");
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-resolution: 2dppx) {
  .advantages::after {
    background-image: url("./../img/pic/advantages-bg@2x.webp");
  }
}

.advantages__heading {
  margin: 0;
  padding: 0;
  font-weight: 600;
  font-size: 28px;
  line-height: 42px;
  text-align: center;
  margin-bottom: 50px;
}
@media (max-width: 1023px) {
  .advantages__heading {
    font-size: 26px;
    line-height: 39px;
    margin-bottom: 48px;
  }
}
@media (max-width: 767px) {
  .advantages__heading {
    font-size: 24px;
    line-height: 36px;
    margin-bottom: 30px;
  }
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
